body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.todo-collection{
    border: none;
}

.todo-collection .todo-item,
.todo-collection .todo-item:last-child {
    border: 1px solid #bdbdbd;
    margin-bottom: 10px;
}

.todo-collection .todo-item .remove-btn {
    margin-left: auto;
    border: 1px solid #333;
    box-shadow: none;
    cursor: pointer;
}

.todo-collection .todo-item .remove-btn i {
    color: #333;
}

.todo-collection .todo-item .remove-btn:hover i {
    color: #df1313;
}

.todo-collection .todo-item .remove-btn:hover {
    border: 2px solid #df1313;
}


.todo-form label {
    color: #595959;
}

.todo-form .todo-input:focus {
    border-bottom: 1px solid #df1313 !important;
    box-shadow: 0 1px 0 0 #df1313 !important;
}
.todos-container {
    max-width: 500px;
    margin: 10px auto;
    padding: 10px 20px;
}

.todos-container h3 {
    text-align: center;
    margin-bottom: 30px;
}

.line-through {
    text-decoration: line-through;  /* for support ie*/
    -webkit-text-decoration: line-through #df1313 ;
            text-decoration: line-through #df1313 ; /* for support other browsers*/
}
